import $api from '../http'
import {AxiosResponse} from 'axios'
import { AuthResponse } from '../models/response/AuthResponse'
import { IEvent } from '../models/IEvent'
import { runInNewContext } from 'vm'

export default class EventService {
    static fetchEvents() : Promise<AxiosResponse<IEvent[]>> {
        return $api.get('/events')
    }  

    static async createEvent( name: string, creator: string, desc: string, genres: string[], date: Date, adress: string, participants: string[]) : Promise<AxiosResponse<IEvent[]>> {
        return $api.post('/events', {name, creator, desc, genres, date, adress, participants})
    }
    
    static async findEvent( searchValue:string, searchType:string ){
        return $api.post('/events-find', { searchValue, searchType })
    }
}