import { makeAutoObservable } from "mobx";
import { TypeVariable } from "typescript";
import { IUser } from "../models/IUser";
import AuthService from "../services/AuthService";
import axios from 'axios'
import { AuthResponse } from "../models/response/AuthResponse";
import { API_URL } from "../http";
import UserService from "../services/UserService";

export default class Store {
    user = {} as IUser
    isAuth = false
    
    constructor(){
        makeAutoObservable(this)
    }
    
    setAuth(bool:boolean){
        this.isAuth = bool
    }
    
    setUser(user:IUser){
        this.user = user
    }
    
    async loginF(email:string, password:string){
        try {
            const response = await AuthService.loginF(email, password)
            console.log(response)
            localStorage.setItem('token', response.data.accessToken)
            this.setAuth(true)
            this.setUser(response.data.user)
        } catch (error:any) {
            console.log(error.response?.data?.message)
        }
    }
    
    async registration(login:string, email:string, password:string, birthday:Date, cell:string, city:string, gender:string){
        try {
            const response = await AuthService.registration(login, email, password, birthday, cell, city, gender)
            console.log(response)
            localStorage.setItem('token', response.data.accessToken)
            this.setAuth(true)
            this.setUser(response.data.user)
        } catch (error:any) {
            console.log(error.response?.data?.message)
        }
    }
    
    async logout(){
        try {
            const response = await AuthService.logout()
            localStorage.removeItem('token')
            this.setAuth(false)
            this.setUser({} as IUser)
        } catch (error:any) {
            console.log(error.response?.data?.message)
        }
    }
    
    async checkAuth(){
        try {
            const response = await axios.get<AuthResponse>(`${API_URL}/refresh`, {withCredentials: true})
            console.log(`Refresh response: ` + response)
            localStorage.setItem('token', response.data.accessToken)
            this.setAuth(true)
            this.setUser(response.data.user)
        } catch (error:any) {
            console.log(error.response?.data?.message)
        }
    }
    
    async changeAvatar( filedata:File ){
        try{
            UserService.changeAvatar(filedata)
        } catch (error:any) {
            console.log(error.response?.data?.message)
        }
    }
}